<template lang="html">
  <div class="friend-box" @click="toDetail()">
    <div class="friend-left">
      <img v-if="data.userType" :src="data.logo || this.defaultImg" alt="">
      <img v-else-if="parseInt(data.userType) === 0 && parseInt(data.sex) === 0" :src="this.defaultLogo" alt="">
      <span v-else class="friend-left__pic" :class="{'woman': data.sex === 2}">{{data.realName | toFormatName}}</span>
    </div>
    <div class="friend-center">
      <div class="friend-info">
        <div class="friend-info__name">{{data.realName || ''}}</div>
        <div class="">{{data.mobile}}</div>
        <div class="mobile-times" v-if="false">
          <span class="month">30</span>
          <img src="../../assets/img/ip@3x.png" alt="">
          <span>{{data.mobileTimes}}</span>
        </div>
      </div>
    </div>
    <div class="friend-arrow-right" @click.stop="moveFriend(data.id)">
      <span></span>
      <img class="" src="../../assets/img/y01@3x.png" alt="">
    </div>
  </div>
</template>

<script>
import utils from '@/utils/utils'
import hzl from '@/assets/img/hzl@2x.png'
import logo from '@/assets/img/logo.png'
export default {
  props: {
    data: {
      type: Object,
      default: {
        // logo: 'https://ss0.baidu.com/6ONWsjip0QIZ8tyhnq/it/u=2574774309,856884605&fm=58&s=7A61F148D883984908165216030050DF&bpow=121&bpoh=75',
        // id: '11',
        // mobile: '1231234212',
        // mobileTimes: 2,
        // realName: '',
        // sex: 2,
        // userType: 1
      }
    }
  },
  data () {
    return {
      defaultImg: hzl,
      defaultLogo: logo
    }
  },
  computed: {
    userId: function () {
      return utils.userId()
    }
  },
  methods: {
    moveFriend (collectId) {
      this.$router.push({path: 'movefriend', query: {id: collectId}})
    },
    toDetail () {
      sessionStorage.setItem('searchId', this.data.collectId)
      this.$router.push('/detail')
    }
  }
}
</script>

<style lang="less">
@import '../../assets/css/variable.less';
.friend-box {
  padding: 0.15rem .1rem;
  overflow: hidden;
  position: relative;
  border-bottom: 1px solid @lineColor;
  background: @whiteColor;
  .friend-left {
    float: left;
    width: 1rem;
    padding-right: .2rem;
    padding-left: .2rem;
    border-right: 1px solid @lineColor;
    height: .8rem;
    line-height: .8rem;
    img {
      width: .6rem;
      height: .6rem;
      border-radius: 50%;
      vertical-align: middle;
    }
    .friend-left__pic {
      display: inline-block;
      width: .6rem;
      height: .6rem;
      font-size: .32rem;
      border-radius: 50%;
      line-height: .6rem;
      text-align: center;
      color: @whiteColor;
      background: @manColor;
      &.woman {
        background: @womanColor;
      }
    }
  }
  .friend-center {
    margin: 0 .6rem 0 1.2rem;
    font-size: .3rem;
    color: @defaultColor;
    line-height: .44rem;
    overflow: hidden;
    .friend-info {
      float: left;
      .friend-info__name {
        max-width: 3rem;
        height: .3rem;
        line-height: .32rem;
        margin-bottom: .2rem;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
      }
    }
    .mobile-times {
      font-size: .28rem;
      color: @activeColor;
      img {
        margin-left: .2rem;
        width: .46rem;
        height: .46rem;
      }
      img, span {
        vertical-align: middle;
      }
      .month {
        color: @lightColor;
      }
    }
  }
  .friend-arrow-right {
    position: absolute;
    top: 50%;
    height: 1rem;
    line-height: 1rem;
    right: .08rem;
    transform: translateY(-50%);
    img {
      width: .46rem;
      height: .46rem;
      vertical-align: middle;
    }
    span {
      display: inline-block;
      position: relative;
      top: .02rem;
      font-size: .28rem;
      color: @lightColor;
    }
  }
}
.friend-box:last-child {
  border-bottom: none;
}
</style>



