<template lang="html">
  <div class="">
    <default-head header="" path="/circleindex">
      <router-link slot="head-right" to="/friendcircle">
        <img class="circle-head-img" @click="addGroup" src="../assets/img/circle/add_icon@3x.png" alt="">
      </router-link>
    </default-head>
    <div id="wrapper">
      <div class="circle-search-box">
        <i class="search__icon circle-search__icon"></i>
        <input class="circle-search__input" type="number" name="" v-model="value" @input="filterNumber" @keyup.delete="keyupDelete">
      </div>
      <div class="circle-box">
        <circle-type @add-group="addGroup" :circleArr="circleArr" @select-item="selectItem"></circle-type>
        <div class="circle-right">
          <friend-item v-for="(item, index) in phoneArr" :key="item.index" :data="item"></friend-item>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import api from '@/api'
import utils from '@/utils/utils'
import minutils from '@/utils/minUtils'
import defaultHead from '@/components/head/defaultHead'
import circleType from '@/components/friendCircle/circleType'
import friendItem from '@/components/friendCircle/friendItem'
export default {
  components: { defaultHead, circleType, friendItem },
  data () {
    return {
      value: '',
      groupType: 0,
      circleArr: [],
      phoneArr: []
    }
  },
  mounted () {
    this.initPage()
  },
  computed: {
    userId: function () {
      return utils.userId()
    }
  },
  methods: {
    selectItem (item) {
      this.groupType = item.id
      this.getCollectionList(item.id)
      this.value = ''
    },
    initPage () {
      let top = document.querySelector('.circle-right').offsetTop
      let height = document.body.clientHeight - top
      document.querySelector('.circle-right').style.height = height + 'px'
      document.querySelector('.circle__type').style.height = height + 'px'
      // 
      api.getCollectionTypeList({'userId': this.userId}).then((response) => {
        let result = response.body
        if (result.isSuccess) {
          this.circleArr = [{name: '', id: 0}].concat(response.body.data)
          this.getCollectionList(0)
        }
      })
    },
    addGroup () {
      let self = this
      minutils.MessageBoxPrompt({
        title: '',
        options: {
          inputPlaceholder: '(1-16)'
        },
        success (value, action) {
          if (!value) {
            return
          } else if (value.length <= 16) {
            self.saveGroupList(value)
          }
          // /^[\u4e00-\u9fa5]+$/i.test(value)
        },
        fail (action) {
        }
      })
    },
    // 
    saveGroupList (value) {
      api.saveCollectionTypeList({'userId': this.userId, 'name': value, 'sortBy': 1}).then((response) => {
        this.getGroupList()
      })
    },
    // 
    getGroupList () {
      api.getCollectionTypeList({'userId': this.userId}).then((response) => {
        this.circleArr = [{name: '', id: 0}].concat(response.body.data)
      })
    },
    // 
    getCollectionList (type, callback, e) {
      minutils.IndicatorOpen('...')
      api.getCollectionList({'userId': this.userId, 'type': type}).then((response) => {
        minutils.IndicatorClose()
        this.phoneArr = [].concat(response.body.data.collectList)
        typeof callback === 'function' && callback(e)
      })
    },
    filterNumber (e) {
      this.phoneArr = this.phoneArr.filter(function (item) {
        return item.mobile.indexOf(e.target.value) !== -1
      })
    },
    keyupDelete (e) {
      let self = this
      this.getCollectionList(this.groupType, function () {
        self.filterNumber(e)
      })
    }
  }
}
</script>

<style lang="less">
@import '../assets/css/variable.less';
.circle-head-img {
  width: .46rem;
  height: .46rem;
  float: right;;
}
.circle-search-box {
  background:@whiteColor;
  padding: .22rem;
  position: relative;
  .circle-search__input {
    width: 100%;
    line-height: .68rem;
    border: none;
    background-color: @bgColor;
    border-radius: .05rem;
    text-indent: .6rem;
  }
  .circle-search__icon {
    top: 50%;
    left: .3rem;
    transform:translateY(-50%);
  }
}
.circle-box {
  background: @whiteColor;
  margin-top: .3rem;
  // border-top: 1px solid @lineColor;
  overflow-y: auto;
  .circle-right {
    position: absolute;
    left: 1.6rem;
    height: 100%;
    width: 5.9rem;
    overflow-y: auto;
  }
}
</style>



