<template lang="html">
  <ul class="circle__type">
    <li v-for="item in circleArr" :class="{'active': active === item.name }"@click="selectItem(item)" class="circle__type__item">{{item.name}}</li>
    <!-- <li class="circle__type__item">
      <div @click="addGroup" class="circle-item-last-box">
        <img src="../../assets/img/head/add@3x.png" alt="">
        <span></span>
      </div>
    </li> -->
  </ul>
</template>

<script>
export default {
  props: {
    circleArr: {
      type: Array,
      default: []
    }
  },
  data () {
    return {
      active: ''
    }
  },
  methods: {
    selectItem (item) {
      this.active = item.name
      this.$emit('select-item', item)
    },
    addGroup () {
      this.$emit('add-group')
    }
  }
}
</script>

<style lang="less">
@import '../../assets/css/variable.less';
  .circle__type {
    position: absolute;
    left: 0;
    top: 2.3rem;
    height: 100%;
    width: 1.6rem;
    overflow-y: scroll;
    background: @circleTypeBgcolor;
    .circle__type__item{
      font-size: .32rem;
      color: @circleTypeColor;
      line-height: .88rem;
      height: .88rem;
      text-overflow: ellipsis;
      overflow-x: hidden;
      // border-bottom: 1px solid @whiteColor;
      text-align: center;
      width: 100%;
    }
    .circle__type__item:last-child {
      border-bottom: none;
    }
    .circle-item-last-box {
      position: absolute;
      left: 50%;
      transform: translate(-50%, 50%);
      white-space: nowrap;
      img {
        width: .46rem;
        height: .46rem;
      }
      img, span {
        vertical-align: middle;
      }
    }
    .active {
      background: @whiteColor;
      color: @defaultColor;
    }
  }
</style>



